












































































import axios from "axios";

export default {
    data() {
        return {
            item: {},
            dialog: false,
            loading: {
                data: false,
                resource: false,
            },
            items: [],
            headers: [
                {
                    text: this.$t("admin.category.fields.name"),
                    value: 'name'
                },
                {
                    text: this.$t("admin.category.fields.created_at"),
                    value: 'created_at'
                },
                {
                    text: this.$t("general.actions"),
                    value: 'actions'
                },
            ]
        }
    },
    created() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            this.loading.resource = true;

            axios.get("/api/categories").then((response) => {
                this.loading.resource = false;

                this.items = response.data;
            })
        },
        openDialog(item) {
            this.item = item;
            this.dialog = true;
        },
        deleteItem() {
            this.loading.data = true;

            axios.delete(`/api/categories/${this.item.id}`).then(() => {
                this.loading.data = false;
                this.dialog = false;
                this.fetchData();
            })
        }
    }
}
