






































































import axios from "axios";

export default {
    props: {
        id: {
            required: true
        }
    },
    created() {
        this.loading = true;

        axios.get(`/api/books/${this.id}`).then((response) => {
            this.loading = false;

            this.record = response.data;
        })
    },
    data() {
        return {
            loading: false,
            record: {}
        }
    }
}
